body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #348F50;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #56B4D3, #348F50);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #56B4D3, #348F50);
  background: linear-gradient(to left, #56B4D3, #348F50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #dcd6d6;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #dcd6d6;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #dcd6d6;
}
:-moz-placeholder { /* Firefox 18- */
  color: #dcd6d6;
}

@font-face {
    font-family: 'MrRobot';
    font-style: normal;
    font-weight: normal;
    src: local('MrRobot'), url(/RoboLove/static/media/MRROBOT.86c545cc.woff) format('woff');
}

* {
    margin: 0;
    padding: 0;
}

h1 {
    margin-bottom: 1rem;
    color: #73f79c;
    font-weight: 200;
    font-family: 'MrRobot';
}
